import Recommend from "@/components/Recommend";
import {
	express,
	orderDetail
} from "@/api/order";
import {
	copyClipboard
} from "@/utils";

const NAME = "Logistics";

export default {
	name: NAME,
	components: {
		Recommend
	},
	data: function() {
		return {
			id: "",
			cartInfo: [],
			orderInfo: {},
			expressList: [],
			loaded: false,
			logisticsList: [{
				message: "暂无数据",
				messageDate: ""
			}]
		};
	},
	watch: {
		$yroute(n) {
			if (n.name === NAME && this.$yroute.query.id !== this.id) {
				this.id = this.$yroute.query.id;
				this.getExpress();
			}
		}
	},
	mounted: function() {
		this.id = this.$yroute.query.id;
		this.getExpress();
	},
	methods: {
		copyClipboard,
		getExpressInfo() {
			let params = {
				orderCode: this.id,
				shipperCode: this.orderInfo.deliverySn,
				logisticCode: this.orderInfo.deliveryId
			};
			express(params)
				.then(res => {
					this.logisticsList = res.data.traces.reverse();
				})
				.catch(err => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});
				});
		},
		getExpress() {
			if (!this.id){
				uni.showToast({
								title: err.msg || err.response.data.msg,
								icon: 'none',
								duration: 2000
							});
							return
			} 
			this.loaded = false;
			orderDetail(this.id)
				.then(res => {
					this.orderInfo = {
						deliveryId: res.data.deliveryId,
						deliveryName: res.data.deliveryName,
						deliverySn: res.data.deliverySn
					};
					this.getExpressInfo();
					// const result = res.data.express.result || {};
					// this.cartInfo = res.data.order.cartInfo;
					// this.expressList = result.list || [];
					// this.loaded = true;

				})
				.catch(err => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});
				});
		}
	}
};
